  .form-group,
  .form-group label,
  .form-group input {
    width: 100%;
    margin-bottom: 0.5rem;
  }
  
  .form-group label {
    font-size: 0.8rem;
  }
  
  .form-group input {
    padding: 10px 20px;
    border: none;
    border-bottom: 3px solid transparent;
    background-color: #eee;
    outline-width: 0;
    font-size: 1rem;
  }
  
  .form-group input:focus {
    border-bottom: 3px solid #ba9bc4;

  }
  
